






































import { Vue, Component } from 'vue-property-decorator';

import { ServiceI18n, StorageLanguage } from '@/../helpers';
import { IComponent } from '@/../models';

import { II18n, I18nOptions } from '@/assets/i18n';
import { WebPagePath } from '@/config';

import * as Rx from 'rxjs';
import * as RxOperator from 'rxjs/operators';
import { setting$ } from '@/stores';

interface II18nSrc extends IComponent.ISelectionBase {
    src: string;
}

@Component({
    components: {},
})
export default class Language extends Vue {
    //#region Variables
    private showI18n: boolean = false;
    private i18nSrc: string = require('@/assets/images/flags/en-US.png');
    private i18nData: II18nSrc[] = [];

    private languageDefault: II18nSrc = {
        objectId: II18n.ELanguage['en-US'],
        name: I18nOptions['en-US'],
        src: require('@/assets/images/flags/en-US.png'),
    };
    private language: II18nSrc = JSON.parse(JSON.stringify(this.languageDefault));
    private languageOptions: II18nSrc[] = [];

    private nowLanguage: string = StorageLanguage.getStorage();
    private languageDir: { [key: string]: string } = { 'en-US': 'English' };

    private stop$: Rx.Subject<{}> = new Rx.Subject();
    private showI18nKey: boolean = false;
    private i18nMode: string = '';

    //#endregion Variables

    //#region Vue Life
    private beforeCreate(): void {}
    private created(): void {
        this.getIsI18nKey();
    }
    private beforeMount(): void {
        this.initLanguage();
    }
    private mounted(): void {}
    private beforeDestroy(): void {}
    private destroyed(): void {}
    //#endregion Vue Life

    //#region Funation
    private getIsI18nKey(): void {
        setting$.pipe(RxOperator.takeUntil(this.stop$)).subscribe({
            next: (x) => {
                this.showI18nKey = x.showI18nKey === true ?? false;
                this.i18nMode = !!x.i18nMode ? x.i18nMode : '';

                this.initLanguageSelction();
                this.initLanguage();
                this.updateStorageLanguage();
            },
        });
    }

    private clickI18n(): void {
        this.showI18n = !this.showI18n;
    }

    private updateStorageLanguage(): void {
        let tempNowLanguage = StorageLanguage.getStorage();

        let isCorrectI18nMode: boolean = false;

        for (const key in this.languageDir) {
            if (key.split('$')[1] === this.i18nMode) {
                isCorrectI18nMode = true;
                break;
            }
        }

        // have i18n mode
        if (!!this.i18nMode) {
            if (tempNowLanguage.split('$').length === 1 && isCorrectI18nMode) {
                tempNowLanguage = `${tempNowLanguage}$${this.i18nMode}`;
            }

            if (tempNowLanguage.split('$').length === 2) {
                if (tempNowLanguage.split('$')[1] === this.i18nMode) {
                    tempNowLanguage = tempNowLanguage;
                } else {
                    tempNowLanguage = tempNowLanguage.split('$')[0];
                }
            }
        } else {
            // no have i18n mode
            if (tempNowLanguage.includes(this.i18nMode) && tempNowLanguage.split('$')[1] !== this.i18nMode) {
                tempNowLanguage = tempNowLanguage.split('$')[0];
            }
        }

        this.selectLanguage(tempNowLanguage, 'push');
    }

    private initLanguageSelction(): void {
        delete this.languageDir[II18n.ELanguage['en-US']];
        let originalLanguage = JSON.parse(JSON.stringify(ServiceI18n.languageI18n));
        let tempModeDir = {};
        let tempNoModeDir = {};
        let modeKey: string = '';

        for (const [key, value] of Object.entries(originalLanguage)) {
            let keySplits = key.split('$');
            let i18nKey = key.split('$')[0];
            modeKey = key.split('$')[1];

            if (keySplits.length > 2) {
                continue;
            }

            if (keySplits.length === 2 && keySplits[1] === this.i18nMode) {
                tempModeDir[key] = value;
            } else {
                tempNoModeDir[i18nKey] = value;
            }

            if (key.includes(II18n.ELanguage['i18n-key'])) {
                tempModeDir[key] = value;
                tempNoModeDir[key] = value;
            }
        }

        this.languageDir = !!this.i18nMode && this.i18nMode === modeKey ? tempModeDir : tempNoModeDir;
    }

    private initLanguage(): void {
        if (!this.showI18nKey) {
            delete this.languageDir[II18n.ELanguage['i18n-key']];
        }

        let storageLanguage = StorageLanguage.getStorage();
        this.languageOptions = [];

        // get language options
        let storageIsSupport: boolean = false;
        let originalLanguageKeys: string[] = Object.keys(this.languageDir);

        for (const key of originalLanguageKeys) {
            let tempData: II18nSrc = {
                objectId: key,
                name: this.languageDir[key],
                src: require(`@/assets/images/flags/${key.split('$')[0]}.png`),
            };

            this.languageOptions.push(tempData);

            if (storageLanguage && storageLanguage === tempData.objectId) {
                storageIsSupport = true;
                this.language = JSON.parse(JSON.stringify(tempData));
                this.i18nSrc = tempData.src;
            }
        }
    }

    private isSelectedI18n(selectedI18n: string): boolean {
        return this.nowLanguage === selectedI18n;
    }

    private selectLanguage(id: string, $routerWay: 'go' | 'push' = 'go'): boolean {
        const selectedI18n: string = this.languageOptions.filter((item) => id === item.objectId)[0].objectId;
        if (this.nowLanguage === selectedI18n) return false;

        let router: any = this.$router;
        let tempdata: II18nSrc = undefined;
        if (id) {
            tempdata = this.languageOptions.filter((item) => id === item.objectId)[0];
            this.language = tempdata;
            this.i18nSrc = tempdata.src;
            StorageLanguage.saveStorage(id);
        } else {
            this.language = JSON.parse(JSON.stringify(this.languageDefault));
            this.i18nSrc = require('@/assets/images/flags/en-US.png');
        }

        ServiceI18n.setVueI18n();

        let path = JSON.parse(JSON.stringify(router?.history?.current?.fullPath));
        if (!!path) {
            if ($routerWay) {
                this.$router.go(path);
            } else {
                this.$router.push(WebPagePath.Empty);
                this.$router.push(path);
            }
        }

        this.nowLanguage = StorageLanguage.getStorage();
    }

    //#endregion Funation
}
