







































































import { Vue, Component, Prop } from 'vue-property-decorator';

import { IComponent } from '@/../models';
import { StorageUser, ServiceServerConfig } from '@/../helpers';

import { IStorage } from '@/../models';
import { ServerApiPath, WebPagePath } from '@/config/path';

import Loading from '@/../components/loading/loading.vue';
import Language from '@/../components/language/language.vue';
import UserMessage from '@/../components/user_message/user_message.vue';

interface IHeaderItemItem {
    username: string;
}

@Component({
    components: { UserMessage, Language },
})
export default class HeaderContainer extends Vue {
    //#region prop
    @Prop({
        type: Boolean,
        default: () => false,
    })
    private showUserImage: boolean;

    @Prop({
        type: Boolean,
        default: () => true,
    })
    private showMessage: boolean;

    @Prop({
        type: Boolean,
        default: () => true,
    })
    private showLanguage: boolean;

    @Prop({
        type: String,
        default: () => '',
    })
    private userImage: string;

    @Prop({
        type: Object,
        default: () => IComponent.DefaultShowUserItem,
    })
    private showUserItem: IComponent.IShowUserItem;
    //#endregion prop

    //#region variable
    private headerItem: IHeaderItemItem = {
        username: '',
    };

    private loading = new Loading();
    //#endregion variable

    //#region Vue Life
    private beforeCreate(): void {}
    private created(): void {
        this.initUserInfo();
    }
    private beforeMount(): void {}
    private mounted(): void {}
    private beforeDestroy(): void {}
    private destroyed(): void {}
    //#endregion Vue Life

    //#region init
    private initUserInfo(): void {
        let user: any = this.$user;
        if (user.user?.username) {
            this.headerItem.username = user.user.username;
        }
    }
    //#endregion init

    //////////////////// Click ////////////////////

    //#region click
    private clickHamnburger(): void {
        this.$emit('click-hamnburger');
    }
    //#endregion click

    //#region logout
    private async logout(): Promise<boolean> {
        // Clear Session
        let user: IStorage.IUser = StorageUser.getStorage();
        StorageUser.clearStorage();

        if (!user.sessionId) {
            this.$router.push(WebPagePath.Login);
            return false;
        } else {
            // Send API to Server
            const url: string = ServiceServerConfig.url(ServerApiPath.Logout_C);
            const param: { sessionId: string } = { sessionId: user.sessionId };

            try {
                await this.axios.post(url, param);
            } catch (e) {
                console.error('Logout Error: ', e);
            } finally {
                // Change page to login
                this.$router.push(WebPagePath.Login);
            }
        }
    }
    //#endregion logout

    //#region server restart
    private async serverRestart(): Promise<void> {
        this.loading.show();

        const url: string = ServiceServerConfig.paramUrl(ServerApiPath.Server_Bye_R);

        let result: any = await this.axios.post(url).catch((e: any) => {
            setTimeout(() => location.reload(), 1000);
            this.loading.hide();
        });

        if (result && result.status === 200) {
            setTimeout(() => location.reload(), 1000);
            this.loading.hide();
        }
    }
    //#endregion server restart
}
