













//#region Import
//#region Vue
import { Vue, Component } from 'vue-property-decorator';
//#endregion

//#region Module
//#endregion

//#region Framework
import { ServiceRole } from '@/../helpers';
import { IComponent } from '@/../models';
//#endregion

//#region Src
import { WebPagePath, NavListOriginal, INavList } from '@/config';
//#endregion

//#region Views
//#endregion

//#region Components Framework
//#endregion

//#region Components Src
//#endregion

//#region Components Views
//#endregion

//#region vue-sidebar-menu
import VueSidebarMenu from 'vue-sidebar-menu';
import 'vue-sidebar-menu/dist/vue-sidebar-menu.css';
Vue.use(VueSidebarMenu);
//#endregion
//#endregion

interface INavItem {
    collapsed: boolean;
    showOneChild: boolean;
}

@Component({
    components: {},
})
export default class NavSrc extends Vue {
    //#region Prop
    //#endregion

    //#region Variables
    private navList: INavList[] = [];

    private navItem: INavItem = {
        collapsed: false, // sidebar state. true: collapsed. false: not collapsed
        showOneChild: true, // Keep only one child opened at a time (first level only)
    };
    //#endregion

    //#region Computed
    //#endregion

    //#region Watch
    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {
        this.initNavList();
    }
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    //#region Init
    //#region Init nav
    private initNavList(): void {
        this.initNavListOriginalData();
    }

    private initNavListOriginalData(): void {
        this.navList = this.settingNavTitle(JSON.parse(JSON.stringify(NavListOriginal)));
    }

    //#endregion
    //#endregion

    //#region View Event
    //#region Event click
    private async onItemClick(event: Event, item: INavList, node): Promise<void> {
        const router: any = this.$router;
        const currentPath: string = router?.history?.current?.path;

        if (item.href === currentPath) {
            await this.$router.push(WebPagePath.Empty);
            await this.$router.push(item.href);
        }
    }
    //#endregion
    //#endregion

    //#region Other Function
    //#region Setting title
    private settingNavTitle(navList: INavList[]): INavList[] {
        for (let nav of navList) {
            nav.title = this.$i18n[nav.titleI18nKey];

            if (!!nav.child) {
                for (let navChild of nav.child) {
                    navChild.title = this.$i18n[navChild.titleI18nKey];
                }
            }
        }

        return navList;
    }
    //#endregion
    //#endregion
}
Vue.component('nav-src', NavSrc);
