










import { Vue, Component, Prop } from 'vue-property-decorator';

import * as Rx from 'rxjs';
import * as RxOperator from 'rxjs/operators';
import { setting$ } from '@/stores';

@Component({
    components: {},
})
export default class LogoLargeImageSrc extends Vue {
    @Prop({
        type: String,
        default: () => {
            return '';
        },
    })
    private image: string;

    private fileName: string = '';

    private stop$: Rx.Subject<{}> = new Rx.Subject();

    //////////////////// Vue Life ////////////////////
    private async beforeCreate() {}
    private async created() {
        this.initProjectName();
    }
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {
        this.stop$.next();
    }

    private initProjectName(): void {
        setting$.pipe(RxOperator.takeUntil(this.stop$)).subscribe({
            next: (x) => {
                this.fileName = `${x.logo.filename ? __webpack_public_path__ + x.logo.filename : ''}`;
            },
        });
    }

    private async click() {
        this.$emit('click');
    }
}
Vue.component('logo-large-image-src', LogoLargeImageSrc);
