








import { Vue, Component, Prop } from 'vue-property-decorator';
import LogoLargeImageSrc from '@/components/logo/large-image.vue';
import LogoProjectNameSrc from '@/components/logo/project-name.vue';

@Component({
    components: { LogoProjectNameSrc, LogoLargeImageSrc },
})
export default class LogoDefaultSrc extends Vue {
    //////////////////// Vue Life ////////////////////
    private async beforeCreate() {}
    private async created() {}
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    private async click() {
        this.$emit('click');
    }
}
Vue.component('logo-default-src', LogoDefaultSrc);
