







































import { Vue, Component, Prop } from 'vue-property-decorator';

import { IComponent, IStorage } from '@/../models';
import { ServiceI18n, personEmpty, ServiceServerConfig, StorageUser, ServiceUser, ServiceDialog, ServiceResponseFilter } from '@/../helpers';
import { WebPagePath, ServerApiPath } from '@/config';

// Container
import BridleContainer from '@/../containers/bridle.vue';
import LogoDefaultSrc from '@/components/logo/default.vue';
import MessageSrc from '@/components/header/message.vue';
import NavSrc from '@/components/nav/nav.vue';
import FooterSrc from '@/components/footer/footer.vue';
import UserMessage from '@/../components/user_message/user_message.vue';
import Loading from '@/../components/loading/loading.vue';

interface IHeaderItem {
    showUserImage: boolean;
    showLanguage: boolean;
    showMessage: boolean;
    userImage: string;
}

@Component({
    components: {
        BridleContainer,
        MessageSrc,
        LogoDefaultSrc,
        NavSrc,
        FooterSrc,
        UserMessage,
    },
})
export default class Bridle extends Vue {
    private headerItem: IHeaderItem = {
        showUserImage: true,
        showLanguage: true,
        showMessage: true,
        userImage: '',
    };

    private loading = new Loading();

    //////////////////// Vue Life ////////////////////

    private async beforeCreate() {}
    private async created() {
        ServiceI18n.setDefaultLanguage();
        // this.checkLoginStatus();
        this.headerItem.userImage = this.$user?.user?.imageBase64 || personEmpty;
    }
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    private goToHome() {
        if (this.$router.currentRoute.name !== 'Home') {
            this.$router.push(WebPagePath.Root);
        }
    }

    //////////////////// Vue Life ////////////////////

    private logout(): void {
        // Clear Session
        let user: IStorage.IUser = StorageUser.getStorage();
        StorageUser.clearStorage();

        this.$router.push(WebPagePath.Login);
    }

    private async serverRestart(): Promise<void> {
        this.loading.show();

        await this.delay(500);

        location.reload();
    }

    private async delay(ms: number): Promise<void> {
        await new Promise<void>((resolve) => {
            setTimeout(() => {
                return resolve();
            }, ms);
        });
    }

    private async checkLoginStatus() {
        // No Session
        if (!this.$user.sessionId || this.$user.sessionId == '') {
            this.$router.push(WebPagePath.Login);
            return false;
        }

        // Check Session Expired
        let url: string = ServiceServerConfig.url(ServerApiPath.Login_C);
        let param: {
            sessionId: string;
        } = {
            sessionId: this.$user.sessionId,
        };
        const CONTENT_RESPONSEFAIL: string = this.$i18n.Error_Response_Fail;
        const CONTENT_200_ERROR: string = this.$i18n.Error_Response_200_NoSessionId;
        const CONTENT_401: string = this.$i18n.Error_Response_401;
        const CONTENT_403: string = this.$i18n.Error_Response_403;
        const CONTENT_500: string = this.$i18n.Error_Response_500;
        const TITLE_ERROR: string = this.$i18n.Status_Error;

        await this.axios
            .post(url, param)
            .then((response: any) => {
                let statusCode = response.status;
                let data = response.data;
                if (statusCode && data) {
                    if (statusCode != 200) {
                        let errorMessage = CONTENT_200_ERROR;
                        switch (statusCode) {
                            case 401:
                                errorMessage = CONTENT_401;
                                break;
                            case 403:
                                errorMessage = CONTENT_403;
                                break;
                            case 500:
                                errorMessage = CONTENT_500;
                                break;
                        }
                        console.error(`!!! ${errorMessage}`, response);
                        ServiceDialog.ErrorConfirmOnly(this, errorMessage, TITLE_ERROR).then((result: any) => {
                            this.$router.push(WebPagePath.Login);
                        });
                    }

                    if (data.sessionId && data.user) {
                        StorageUser.saveStorage(data);
                        ServiceUser.initVueUser();
                    } else {
                        ServiceDialog.ErrorConfirmOnly(this, CONTENT_200_ERROR, TITLE_ERROR).then((result: any) => {
                            this.$router.push(WebPagePath.Login);
                        });
                        console.error(`!!! ${CONTENT_200_ERROR}`, response);
                    }
                } else {
                    ServiceDialog.ErrorConfirmOnly(this, CONTENT_200_ERROR, TITLE_ERROR);
                    console.error(`!!! ${CONTENT_RESPONSEFAIL}`, response);
                }
            })
            .catch((e: any) => ServiceResponseFilter.catchError(this, e));
    }
}

Vue.component('bridle', Bridle);
