




































import { Vue, Component, Prop } from 'vue-property-decorator';

@Component({
    components: {},
})
export default class ScrollTop extends Vue {
    private showGoTopIcon: boolean = false;
    private scrollTop: number = 0;
    private goTopSrc: string = require('@/../resources/images/up-arrow.png');
    private goBottomSrc: string = require('@/../resources/images/down-arrow.png');

    private isDisableClick: boolean = false;

    private speed: number = 1000;

    //////////////////// Vue Life ////////////////////
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {
        this.$nextTick(() => window.addEventListener('scroll', this.showScrollToIcon, true));
    }
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {
        window.removeEventListener('scroll', this.showScrollToIcon, true);
    }

    private showScrollToIcon(): void {
        let dom: Element = document.getElementsByClassName('bridle__content')[0];

        this.scrollTop = dom.scrollTop;

        if (this.scrollTop > 60) {
            this.showGoTopIcon = true;
        } else {
            this.showGoTopIcon = false;
        }
    }

    private scrollToTop(speed: number): void {
        let dom: Element = document.getElementsByClassName('bridle__content')[0];
        //i
        dom.scrollTop -= speed;

        let timer: NodeJS.Timeout;
        if (dom.scrollTop > 0) {
            timer = setTimeout(() => this.scrollToTop(speed), 16);
            this.isDisableClick = true;
        } else {
            clearTimeout(timer);
            this.isDisableClick = false;
        }
    }

    private scrollToBottom(speed: number): void {
        let dom: Element = document.getElementsByClassName('bridle__content')[0];
        let clientHeight: number = dom.clientHeight;
        let scrollHeight: number = dom.scrollHeight;
        let height: number = scrollHeight - clientHeight; //scrolTop

        Math.ceil((dom.scrollTop += speed));
        let timer: NodeJS.Timeout;
        if (Math.ceil(dom.scrollTop) < height) {
            timer = setTimeout(() => this.scrollToBottom(speed), 16);
            this.isDisableClick = true;
        } else {
            clearTimeout(timer);
            this.isDisableClick = false;
        }
    }
}
Vue.component('scroll-bottom', ScrollTop);
