



















// import { setting } from '@/../data-center/setting';
import { Vue, Component } from 'vue-property-decorator';
import PackageJson from '@/package.json';

import { ServiceServerConfig } from '@/../helpers';
import { ServerApiPath } from '@/config';

import * as Rx from 'rxjs';
import * as RxOperator from 'rxjs/operators';
import { setting$ } from '@/stores/state';

interface IFooterItem {
    webVersion: string;
    serverVersion: string;
    copyright: string;
}

@Component({
    components: {},
})
export default class FooterSrc extends Vue {
    private footerItem: IFooterItem = {
        webVersion: 'v1.00.00',
        serverVersion: 'v1.00.00',
        copyright: 'copyright  Advantech Solution 2021',
    };

    private copyright: string = '';

    private stop$: Rx.Subject<{}> = new Rx.Subject();

    //////////////////// Vue Life ////////////////////
    private beforeCreate(): void {}
    private async created() {
        this.initVersion();
        this.initCopyright();
        await this.initCopyright();
    }
    private beforeMount(): void {}
    private mounted(): void {}
    private beforeDestroy(): void {}
    private destroyed(): void {}

    private initCopyright(): void {
        setting$.pipe(RxOperator.takeUntil(this.stop$)).subscribe({
            next: (x) => {
                this.copyright = x.copyright;
            },
        });
    }

    private async initVersion(): Promise<void> {
        this.initWebVersion();
        await this.initServerVersion();
    }

    private initWebVersion(): void {
        this.footerItem.webVersion = `v${PackageJson.version}`;
    }

    private async initServerVersion(): Promise<void> {
        // const url: string = ServiceServerConfig.paramUrl(ServerApiPath.ServerInfo_R);
        // let { data, status }: any = await this.axios.get(url).catch((e: any) => {});

        // if (status === 200) {
        //     this.footerItem.serverVersion = `v${data.split('_')[1] ?? 'v1.00.00'}`;
        // }

        this.footerItem.serverVersion = `v1.00.01`;
    }
}
Vue.component('footer-src', FooterSrc);
