































































import { Vue, Component, Prop } from 'vue-property-decorator';

import { IStorage } from '@/../models';
import { StorageUser } from '@/../helpers';
import { WebPagePath } from '@/config';

import HeaderContainer from '@/../components/header/header.vue';
import UserMessage from '@/../components/user_message/user_message.vue';
import Footer from '@/../components/footer/footer.vue';
import Nav from '@/../components/nav/nav.vue';
import ScrollTop from '@/../components/scroll/scroll-top.vue';

interface IHamburgerItem {
    isOpen: boolean;
}

@Component({
    components: { HeaderContainer, UserMessage, Footer, Nav, ScrollTop },
})
export default class BridleContainer extends Vue {
    @Prop({
        type: Boolean,
        default: () => true,
    })
    private showUserImage: boolean;

    @Prop({
        type: Boolean,
        default: () => true,
    })
    private showMessage: boolean;

    @Prop({
        type: Boolean,
        default: () => true,
    })
    private showLanguage: boolean;

    @Prop({
        type: String,
        default: () => '',
    })
    private userImage: string;

    @Prop({
        type: Boolean,
        required: false,
        default: () => true,
    })
    private isCheckSessionId: boolean;

    private hamburgerItem: IHamburgerItem = {
        isOpen: true,
    };

    //////////////////// Vue Life ////////////////////
    private beforeCreate(): void {}
    private created(): void {
        if (this.isCheckSessionId) {
            this.checkLogin();
        }
    }
    private beforeMount(): void {}
    private mounted(): void {}
    private beforeDestroy(): void {}
    private destroyed(): void {}

    //////////////////// Click ////////////////////

    private checkLogin(): void {
        let user: IStorage.IUser = StorageUser.getStorage();
        if (!user.sessionId) {
            setTimeout(() => this.$router.push(WebPagePath.Login), 100);
        }
    }

    private clickHamburger(): void {
        this.hamburgerItem.isOpen = !this.hamburgerItem.isOpen;
    }
}
Vue.component('bridle-container', BridleContainer);
