





























































































































import { Vue, Component, Prop } from 'vue-property-decorator';

import { IComponent } from '@/../models';
import { ServiceRole } from '@/../helpers';
import { WebPagePath } from '@/config/path';

import Loading from '@/../components/loading/loading.vue';

interface IUserItem {
    account: string;
    userImageSrc: string;
    skinToggleColor: string;
}

@Component({
    components: {},
})
export default class UserMessage extends Vue {
    //#region prop
    @Prop({
        type: Object,
        default: () => IComponent.DefaultShowUserItem,
    })
    private showUserItem: IComponent.IShowUserItem;
    //#endregion prop

    //#region variable
    private userItem: IUserItem = {
        account: '',
        userImageSrc: '',
        skinToggleColor: '#00C7E6',
    };

    private isShowRestart: boolean = null;

    private showUserInfo: boolean = false;
    private loading = new Loading();
    //#endregion variable

    //#region Vue Life
    private beforeCreate(): void {}
    private created(): void {}
    private beforeMount(): void {}
    private mounted(): void {
        setTimeout(() => this.initUserInfo(), 100);
    }
    private beforeDestroy(): void {}
    private destroyed(): void {}
    //#endregion Vue Life

    //#region init
    private initUserInfo(): void {
        let user: any = this.$user;
        this.userItem.account = user.user?.username ?? '';
        if (ServiceRole.haveSystemAdministrator(user.user?.roles) && this.showUserItem.restart) {
            this.isShowRestart = true;
        }
    }
    //#endregion init

    //#region change mode
    private changeMode(): void {
        console.log(' => wait to do ');
    }
    //#region change mode

    //#region logout
    private logout(): void {
        this.$emit('logout');
        this.showUserInfo = false;
    }
    //#endregion logout

    //#region page
    private pageToMyProfile(): void {
        let router: any = this.$router;
        let currentPath: string = router && router.history && router.history.current && router.history.current.path;

        this.showUserInfo = false;
        if (currentPath !== WebPagePath.MyProfile) {
            this.$router.push(WebPagePath.MyProfile);
        }
    }

    private pageToChangePassword(): void {
        let router: any = this.$router;
        let currentPath: string = router && router.history && router.history.current && router.history.current.path;

        this.showUserInfo = false;
        if (currentPath !== WebPagePath.ChangePassword) {
            this.$router.push(WebPagePath.ChangePassword);
        }
    }
    //#endregion page

    //#region server restart
    private serverRestart(): void {
        this.$emit('serverRestart');
        this.showUserInfo = false;
    }
    //#endregion server restart
}
